*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.NewPost{
  display: flex;
}
.NewPost__area{
  background-color: rgb(184, 200, 201);
  resize: none;
  height: 50px;
  border: 3px solid rgb(29, 26, 212);
  padding: 5px;
}
.NewPost__btn{
  width: 100px;
  height: 50px;
  border: 3px solid rgb(29, 26, 212);
  border-left: 0;
  cursor: pointer;
}