.friends__link{
  display: block;
  width: 100%;
  color: #fff;
  font-size: 20px;
  padding-left: 5px;
}
.friends__link::first-letter{
  text-transform: uppercase;
  color: gold;
}
.friends__link.active{
  color: gold;
}
.friends__items{
  display: flex;
  justify-content: space-between;
}