.content__post{
  width: 100%;
  display: flex;

}
.content__post:not(:last-child){
  margin-bottom: 5px;
}
.content__postImg{
  display: block;
  width: 80px;
}
.content__postImg img{
  display: block;
  height: 80px;
  width: 80px;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
  border: 3px solid #000;
}
.content__post{
}
.content__postText{
  overflow: hidden;
  padding: 2px 0 2px 10px;
  display: block;
  font-family: 'montserrat';
  font-size: 20px;
}