.dialogsItems{
    grid-area: Items;
   background-color: rgb(141, 204, 69);

   padding: 10px;
   display: flex;
   flex-direction: column;

}
.dialogsItem{
   display: block;
   color: #fff;
   font-size: 15px;
   padding-left: 5px;
}
.dialogsItem.active{
   color: rgb(19, 9, 54);
}
.dialogsItem:not(:last-child){
   margin-bottom: 10px;
}