*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
input{
  font-family: inherit;
}
.NewPost{
  display: flex;
  flex-wrap: wrap;
  width: 300px;
  font-family: 'montserrat';
}
.NewPost__area{
  flex: 0 1 70%;
  background-color: rgb(252, 252, 252);
  height: 50px;
  border: none;
  padding: 5px;

}
.NewPost__btn{
  flex: 0 1 30%;
  width: 100px;
  height: 50px;
  border: 0;
  text-transform: uppercase;
  font-size: 25px;
  color: #fff;
  background-color: rgb(61, 29, 29);
  cursor: pointer;
}
.NewPost_input{
  margin-top: 5px;
  flex: 1 1 auto;
  border: 0;
  height: 50px;
  margin-bottom: 20px;
  padding: 5px;
}